<template>
    <div class="card" style="height: 95%;">
        <div class="card__title">
            <b-row>
            <b-col>
                <span>Generate Invoice (Vendor)</span>
            </b-col>
            <b-col>
              <div class='Plan-Header-Bagde'>
              <span> Total : {{M_TotalVendorAmt}} </span>
              </div>
            </b-col>
            <b-col style="max-width: max-content !important;" class="col-right" offset="4">
                <span>
                  <ABSButton
                    :text="'Print'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="doPrint"
                  />
                </span>
                <span>
                  <ABSButton
                    :text="'View All'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="onViewAll"
                  />
                </span>
            </b-col>
            </b-row>
        </div>
        <div class="card__body">
          <div>
              <ACCFormList
                  :prop="propList"
                  :title="''"
                  @rowClicked="rowClicked"
                  @onRenderData="onRenderData"
                  ref="ref_completed_order_vendor"
              >
                <template slot="total_order" slot-scope="data">
                  <div
                    :class="'Plan-Dot-Primary'"
                    :style="'font-size: 10px !important; padding-top: 9px !important;'"
                    >
                      {{data.item.total_order}}
                  </div>
                </template>
                <template slot="total_amount" slot-scope="data">
                  <span>{{isCurrency(data.item.total_amount, decimal)}}</span>
                </template>
              </ACCFormList>
          </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      M_TotalVendorAmt: '0',
      search: "",
      propList: {
        OptionUrl: "/ST/ST_CompletedOrderVendor",
        LineNo: 0,
        initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "'",
        OrderBy: "vendor_name",
        SourceField: "",
        ParamView: "",
        PerPage: 8,
      },
    };
  },
  computed: {},
  methods: {
    rowClicked(record, index) {
        var param = record;
        this.$store.commit("setParamPage", param);
        this.$router.push({ name: "ST_CompletedOrderVendorView" });
    },
    RenderData() {
      this.$refs.ref_completed_order_vendor.doGetList("");
    },
    doPrint() {
      this.$router.push({ name: "ST_CompletedOrderVendorPrint" });
    },
    onViewAll() {
        this.$router.push({ name: "ST_CompletedOrderVendor" });
    },
    onRenderData(data) {
      if (!data) return;
      if (data.length < 1) return;
      // console.log(data)
      if (data[0].total_vendor_amount && data[0].total_vendor_amount !== '') {
        this.M_TotalVendorAmt = this.isCurrency(data[0].total_vendor_amount, this.decimal)
      }
    }
  },
  mounted() {
    this.RenderData();
  },
};
</script>

<style scoped>
</style>